import React from 'react';
import './App.css';
import Main from './Main';
import Signin from './Signin'
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom"
import Signup from './Signup';

function App() {
  return (
    <Router>
      <div className="App">
      <Switch>
          <Route path="/signin">
            <Signin/>
          </Route>
          <Route path="/signup">
            <Signup/>
          </Route>
          <Route path="/">
            <Main />
          </Route>
        </Switch>
      </div>
    </Router>
  )
}

export default App;
